.header {
    background-color: $main-bg;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;
    height: $header-height;
    transition: height 0.3s ease;

    @include tablet {
        height: $header-tablet-height;
        box-shadow: $box-shadow;
    }

    @include mobile {
        height: $header-mobile-height;
    }
    .container {
        height: 100%;
    }

    &.shrink {
        height: $header-shrink-height;
        box-shadow: $box-shadow;
        z-index: 100;

        @include mobile {
            height: $header-mobile-height;
        }
    }

    &__logo {
        @include flex(center, center);
        position: absolute;
        inset: 0;
        pointer-events: none;

        img {
            height: 28px;

        }
    }

    &__menu {
        @include flex(center, space-between);
        height: 100%;
        font-size: 1.5rem;

        &__item ~ &__item {
            margin-left: 39px;
            @include tablet {
                margin-left: 20px;
            }
            @include mobile {
                margin-left: 10px;
            }
        }

        &__item.active {
            font-weight: 600;
            color: $main-color;
        }

        &__item:hover {
            color: $main-color;
        }

        &__left , &__right {
            @include flex(center, center);
            height: 100%;
        }

        &__right {
            font-size: 2.25rem;
            &__item {
                cursor: pointer;
                @include flex(center, center);
                position: relative;
                @include tablet {
                    margin-left: 10px;
                }
                span {
                    @include flex(center, center);
                    color: $txt-white;
                    font-size: 1rem;
                    width: 1.5rem;
                    height: 1.5rem;
                    border-radius: 50%;
                    text-align: center;
                    
                    position: absolute;
                    top: -5px;
                    right: -60%;
                    transform: translateX(-50%);

                    @include mobile {
                        font-size: .8rem;
                        width: 1.25rem;
                        height: 1.25rem;
                        right: -75%;
                    }
                }
            }
            @include mobile {
                font-size: 1.5rem;
            }
        }

        &__left {
            &__close {
                display: none;
            }

            @include tablet {
                flex-direction: column;
                background-color: $main-bg;
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100vh;
                justify-content: flex-start;
                padding-top: 30px;
                transform: translateX(-100%);
                transition: transform 0.5s ease;

                &.active {
                    transform: translateX(0);
                    z-index: 100;
                }

                &__item ~ &__item {
                    margin-left: unset;
                    margin-top: 20px;

                }

                &__close {
                    display: block;
                    position: absolute;
                    left: 20px;
                    font-size: 2.5rem;
                }
            }
        }
        &__mobile-toggle {
            display: none;
            
            @include tablet {
                display: block; 
                font-size: 2.5rem;
            }
        }

    }

}